.profile__avatar-button {
    width: 120px;
    height: 120px;
    border-radius: 120px;
    padding: 0;
    margin: 0;
    border: 0;
    position: relative;
  }
  
  .profile__avatar-button::after {
    content: "";
    background-image: url(../../../images/VectorAvatarHover.svg);
    background-color: #000000;
    opacity: 0;
    background-repeat: no-repeat;
    background-size: 26px;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;  
  }
  
  .profile__avatar-button:hover:after {
    opacity: 0.8;
    cursor: pointer;
  }
  