.popup__title {
  max-width: 330px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  margin-bottom: 44px;
}
@media screen and (max-width: 522px) {
  .popup__title {
      max-width: 238px;
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 70px;
  }
}
